.question-container {
  width: 100%;
  height: calc(100% - 70px);
  position: relative;
  background-color: white;
  overflow-y: auto;
}

.question-back {
  cursor: pointer;
  width: fit-content;
  margin: 2vh auto 2vh 5%;
}

.question-back img {
  width: 2.5rem;
}

.question-box {
  height: fit-content;
  background-color: rgb(255, 253, 243);
  border: 2px solid rgb(255, 247, 207);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1vh 5% 5vh 5%;

}

.question-box-title {
  width: 100%;
  padding: 2vh 2.5% 2vh 2.5%;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.question-box-title-row1 {
  display: flex;
  flex-direction: row;
  justify-content: end;
}

.question-box-title-row1 h4 {
  width: fit-content;
  background-color: #5011d7;
  color: white;
  border-radius: 3px;
  font-size: 1.2rem;
  padding: 0.3rem 2rem 0.3rem 2rem;
  text-align: end;
}

.question-box-title-row2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.question-box-title-row2 img {
  width: 25%;
  margin-right: 3%;
}

.question-box-title-row2 h4 {
  font-size: 1.7rem;
  font-weight: 600;
  color: rgb(116, 103, 69);

  position: relative;
  top: 1rem;
}

.question-box-list-q {
  cursor: pointer;
  width: 90%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: top;
  background-color: rgb(255, 238, 148);
  box-shadow: none;
  border-radius: 10px;
  margin: 1rem 5% 1rem 5%;
  padding: 1rem 1.5rem 1rem 1.5rem;
  transition: box-shadow 0.3s ease;
}

.question-box-list-q:hover {
  box-shadow: 2px 2px 4px 0 rgb(237, 213, 89), -2px -2px 4px 0 rgb(237, 213, 89),
    2px -2px 4px 0 rgb(237, 213, 89), -2px 2px 4px 0 rgb(237, 213, 89);
}

.q-question {
  display: flex;
  flex-direction: row;
  justify-content: start;
  max-width: 80%;
}

.q-question h4,
.q-question h5 {
  font-size: 1.2rem;
  font-weight: 600;
  line-height: 1.5;
  color: #746745;
}

.question-box-list-q p {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1.5;
  color: #878787;
  margin-top: 0.25rem;
}
