.guest-modal {
  width: 70%;
  max-width: 500px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-family: 'GmarketSansMedium';
  box-shadow: 0 0 5px lightgrey;
  border-radius: 20px;
  font-size: 1rem;
  line-height: 1.5;
}

.guest-modal div {
  width: 80%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.guest-modal button {
  margin: 10px;
  background-color: #ffe660;
  border: none;
  font-family: 'Jua';
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 1.1rem;
}
